canvas {
  display: none;
}
body {
  background-color: #fefee3;
  font-family: "Poppins", sans-serif;
  color: #323142;
}

.canvasShow {
  display: block;
  width: 100%;
  height: auto;
}

.camera {
  position: relative;
}

.overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 3;
}

.rectangle {
  width: 200px;
  height: 50px;
  border: 1px solid red;
  /* Center vertically */
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -25px;
  margin-left: -100px;
  z-index: 999;
  background-color: rgba(207, 207, 207, 0.5);
}

video {
  display: none;
}

.accountImage {
  width: 125px;
  height: 125px;
  object-fit: cover;
  border-radius: 50%;
}

.videoShow {
  display: block;
  width: 100%;
  height: auto;
}

.customCard {
  border: none;
  border-radius: 20px;
  -webkit-box-shadow: 0px 0px 38px -27px rgba(102, 102, 102, 1);
  -moz-box-shadow: 0px 0px 38px -27px rgba(102, 102, 102, 1);
  box-shadow: 0px 0px 38px -27px rgba(102, 102, 102, 1);
}

.expirationImg {
  width: 60px;
  height: 60px;
}

.card-horizontal {
  display: flex;
  flex: 1 1 auto;
}
